@import url(https://fonts.googleapis.com/css2?family=Sancreek);

body {
  margin: 0;
  background-color: #19b387;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.desc {
  font-family: monospace;
  font-size: 1.2rem;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.appTitle {
  font-family: sancreek;
  font-size: 4.5rem;
  letter-spacing: 3px;
  color: white;
  text-transform: uppercase;
  -webkit-text-stroke: 2px black;
}

.heading {
  font-family: monospace;
  font-size: 2.4rem;
  position:relative;
  bottom: -20px;
  font-weight: 600;
  letter-spacing: 1px;
  text-transform: uppercase;
}

* {
  scrollbar-width: thin;
  scrollbar-color:  #e9c46a #f4a261;
}

/* Works on Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 12px;
}

*::-webkit-scrollbar-track {
  background: orange;
}

*::-webkit-scrollbar-thumb {
  background-color: #127e5f;
  border-radius: 20px;
  border: 3px solid #f4a261;
}

.todoListMain {
  background-color: #f4a261;
  padding: 10px;
  box-shadow: 5px 5px #127e5f;
  margin-bottom: 15px;
}

.todoListMain .header input {
    padding: 10px;
    font-size: 16px;
    background-color: #f7e5d6;
    border: none;
    width: 80%;
    box-shadow: 5px 5px #e76f51;
  }
  .todoListMain .header input:focus {
    outline: none !important;
    border:2px solid #e76f51;
  }

  .todoListMain .header button {
    padding: 10px;
    font-size: 16px;
    margin: 10px;
    margin-right: 0px;
    background-color: #e9c46a;
    border: none;
    width: 10%;
    box-shadow: 5px 5px #e76f51;
  }
  .todoListMain .header button:hover {
    background-color: #e76f51;
    cursor: pointer;
  }
  .todoListMain .theList {
    list-style: none;
    padding-left: 0;
    text-align: justify;
  } 
  .todoListMain .theList li {
    color: #333;
    background-color: #e9c46a;
    padding: 15px;
    margin: 0px 15px 15px;
    list-style: none;
    box-shadow: 5px 5px #e76f51;

    transition: background-color .2s ease-out;
  }

  .todoListMain .theList li:hover {
    background-color: #e76f51;
  }

  .foodLists {
    max-height: 500px;
    overflow: auto;
  }

  ul.theList {
    padding: 0;
  }

  .delBtn {
    float: right;
    font-size: 22px;
  }

  .delBtn:hover {
    cursor: pointer;
  }
#wrapper {
    margin: 40px auto 0;
    width: 266px;
    position: relative;
  }
  #txt {
    color: #eaeaea;
  }
  #wheel {
    width: 250px;
    height: 250px;
    border-radius: 50%;
    position: relative;
    overflow: hidden;
    border: 8px solid #fff;
    box-shadow: 5px 5px 0px 5px #127e5f;;
    transform: rotate(0deg);
  }
  #wheel:before {
    content: '';
    position: absolute;
    width: 242px;
    height: 242px;
    border-radius: 50%;
    z-index: 1000;
  }
  #inner-wheel {
    width: 100%;
    height: 100%;
    transition: all 6s cubic-bezier(0, .99, .44, .99);
  }
  #wheel .sec {
    position: absolute;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 130px 75px 0;
    border-color: #19c transparent;
    transform-origin: 75px 129px;
    left: 45px;
    top: -15px;
    opacity: 1;
  }
  #wheel .sec:nth-child(1) {
    transform: rotate(60deg);
    border-color: #16a085 transparent;
  }
  #wheel .sec:nth-child(2) {
    transform: rotate(120deg);
    border-color: #2980b9 transparent;
  }
  #wheel .sec:nth-child(3) {
    transform: rotate(180deg);
    border-color: #34495e transparent;
  }
  #wheel .sec:nth-child(4) {
    transform: rotate(240deg);
    border-color: #f39c12 transparent;
  }
  #wheel .sec:nth-child(5) {
    transform: rotate(300deg);
    border-color: #d35400 transparent;
  }
  #wheel .sec:nth-child(6) {
    transform: rotate(360deg);
    border-color: #c0392b transparent;
  }

  #spin {
    width: 68px;
    height: 68px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -34px 0 0 -34px;
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 3px 0px;
    z-index: 1000;
    background: #fff;
    cursor: pointer;
    font-family: "Exo 2", sans-serif;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  #spin:after {
    text-align: center;
    line-height: 68px;
    color: #ccc;
    text-shadow: 0 2px 0 #fff, 0 -2px 0 rgba(0, 0, 0, 0.3);
    position: relative;
    z-index: 100000;
    width: 68px;
    height: 68px;
    display: block;
  }
  #spin:before {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-style:  solid;
    border-width: 0 20px 28px 20px;
    border-color: transparent transparent #fff transparent;
    top: -12px;
    left: 14px;
  }
  #inner-spin {
    width: 54px;
    height: 54px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -27px 0 0 -27px;
    border-radius: 50%;
    z-index: 999;
    box-shadow: rgba(255, 255, 255, 1) 0px -2px 0px inset, rgba(255, 255, 255, 1) 0px 2px 0px inset, rgba(0, 0, 0, 0.4) 0px 0px 5px;
    background: radial-gradient(ellipse at center, rgba(255, 255, 255, 1) 0% , rgba(234, 234, 234, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr = '#ffffff', endColorstr='#eaeaea', GradientType=1);
  }
  #spin:active #inner-spin {
    box-shadow: rgba(0, 0, 0, 0.4) 0px 0px 5px inset;
  }
  #spin:active:after {
    font-size: 15px;
  }
  #shine {
    width: 250px;
    height: 250px;
    position: absolute;
    top: 0;
    left: 0;
    background: radial-gradient(ellipse at center, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0.99) 1%, rgba(255, 255, 255, 0.91) 9%, rgba(255, 255, 255, 0) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr = '#ffffff', endColorstr='#00ffffff', GradientType=1);
    opacity: 0.1;
  }
  @keyframes hh {
    0%, 100% {
      transform: rotate(0deg);
    }
    50% {
      transform: rotate(7deg);
    }
  }
  .spin {
    animation: hh 0.1s;
  }
.raffleBtn {
    padding: 7px 20px;
    margin: 20px 20px 25px;
    background-color: #e76f51;
    border: none;
    box-shadow: 5px 5px #127e5f;
    float: right;
}

.raffleBtn:hover {
    background-color: #f4a261;
    cursor: pointer;
}

.resultBox {
    color: #333;
    background-color: #e9c46a;
    padding: 15px;
    margin: 20px 20px;
    list-style: none;
    box-shadow: 5px 5px #127e5f;
    clear:both;
    visibility: hidden;
}
