#wrapper {
    margin: 40px auto 0;
    width: 266px;
    position: relative;
  }
  #txt {
    color: #eaeaea;
  }
  #wheel {
    width: 250px;
    height: 250px;
    border-radius: 50%;
    position: relative;
    overflow: hidden;
    border: 8px solid #fff;
    box-shadow: 5px 5px 0px 5px #127e5f;;
    transform: rotate(0deg);
  }
  #wheel:before {
    content: '';
    position: absolute;
    width: 242px;
    height: 242px;
    border-radius: 50%;
    z-index: 1000;
  }
  #inner-wheel {
    width: 100%;
    height: 100%;
    transition: all 6s cubic-bezier(0, .99, .44, .99);
  }
  #wheel .sec {
    position: absolute;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 130px 75px 0;
    border-color: #19c transparent;
    transform-origin: 75px 129px;
    left: 45px;
    top: -15px;
    opacity: 1;
  }
  #wheel .sec:nth-child(1) {
    transform: rotate(60deg);
    border-color: #16a085 transparent;
  }
  #wheel .sec:nth-child(2) {
    transform: rotate(120deg);
    border-color: #2980b9 transparent;
  }
  #wheel .sec:nth-child(3) {
    transform: rotate(180deg);
    border-color: #34495e transparent;
  }
  #wheel .sec:nth-child(4) {
    transform: rotate(240deg);
    border-color: #f39c12 transparent;
  }
  #wheel .sec:nth-child(5) {
    transform: rotate(300deg);
    border-color: #d35400 transparent;
  }
  #wheel .sec:nth-child(6) {
    transform: rotate(360deg);
    border-color: #c0392b transparent;
  }

  #spin {
    width: 68px;
    height: 68px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -34px 0 0 -34px;
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 3px 0px;
    z-index: 1000;
    background: #fff;
    cursor: pointer;
    font-family: "Exo 2", sans-serif;
    user-select: none;
  }
  #spin:after {
    text-align: center;
    line-height: 68px;
    color: #ccc;
    text-shadow: 0 2px 0 #fff, 0 -2px 0 rgba(0, 0, 0, 0.3);
    position: relative;
    z-index: 100000;
    width: 68px;
    height: 68px;
    display: block;
  }
  #spin:before {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-style:  solid;
    border-width: 0 20px 28px 20px;
    border-color: transparent transparent #fff transparent;
    top: -12px;
    left: 14px;
  }
  #inner-spin {
    width: 54px;
    height: 54px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -27px 0 0 -27px;
    border-radius: 50%;
    z-index: 999;
    box-shadow: rgba(255, 255, 255, 1) 0px -2px 0px inset, rgba(255, 255, 255, 1) 0px 2px 0px inset, rgba(0, 0, 0, 0.4) 0px 0px 5px;
    background: radial-gradient(ellipse at center, rgba(255, 255, 255, 1) 0% , rgba(234, 234, 234, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr = '#ffffff', endColorstr='#eaeaea', GradientType=1);
  }
  #spin:active #inner-spin {
    box-shadow: rgba(0, 0, 0, 0.4) 0px 0px 5px inset;
  }
  #spin:active:after {
    font-size: 15px;
  }
  #shine {
    width: 250px;
    height: 250px;
    position: absolute;
    top: 0;
    left: 0;
    background: radial-gradient(ellipse at center, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0.99) 1%, rgba(255, 255, 255, 0.91) 9%, rgba(255, 255, 255, 0) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr = '#ffffff', endColorstr='#00ffffff', GradientType=1);
    opacity: 0.1;
  }
  @keyframes hh {
    0%, 100% {
      transform: rotate(0deg);
    }
    50% {
      transform: rotate(7deg);
    }
  }
  .spin {
    animation: hh 0.1s;
  }